@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.heading-container {
    text-align: center;
}

.heading {
    width: 75%;
    margin: 1rem auto;
    font-weight: bolder;
    line-height: 2;
}

.form-container {
    background-color: rgb(138, 138, 228);
    margin: 1rem auto;
    padding : 1rem;
    box-shadow: 5px 5px 5px #ccc;
    border-radius: 5px;
    width: 50%;
}

.form-container form input{
    -webkit-appearance: none;
    display: block;
    margin: 1rem auto;
    border: 2px solid rgb(138, 138, 228);
    border-radius: 5px;
    padding: 0.5rem;
    width: 50%;
    height:50px;
}

.form-container form input::-webkit-outer-spin-button,
.form-container form input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.form-container form input:focus {
    background-color: rgb(215, 215, 238);
    outline: none;
}

.form-container form button {
    display: block;
    margin: 1rem auto;
    padding: 0.5rem;
    background-color: #fff;
    border-radius: 5px;
    color: rgb(138, 138, 228);
    font-weight: bold;
    transition: 0.3s all;
}

.form-container form button:hover {
    background-color: rgb(171, 171, 231) ;
    color: #fff;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    box-shadow: 2px 2px 2px rgb(122, 122, 182);
} 

form p {
    color: red;
    text-align: center;
    font-weight: bold;
}


.nav-header {
    margin: 2rem;
}

.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav-links li {
    margin: 1rem;
    transition: 0.3s all;
}

.nav-links li:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.nav-links a {
    border: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: rgb(137, 137, 199);
  color: white;
}

@media (min-width: 768px) {
    .nav-links {
      flex-direction: row;
    }
  
    .nav-links li {
      margin: 0 0.5rem;
    }
  
    .nav-links a {
      color: black;
      text-decoration: none;
    }
  }
.footer {
    background-color: rgb(137, 137, 199);
    position: relative;
    bottom: 0;
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.col {
    margin: 1rem auto;
}

.logo {
    width: 100px;
}

.element {
    margin-top: 1rem;
}

.element a:active {
    color: white;
}

.icon {
    font-size: 2rem;
    transition: 0.3s all;
}

.icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.footer-socials {
    padding: 1rem;
    line-height:2;
}

.social {
    display: block;
    padding: 0.5rem;
}
.page-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.main-container {
    flex: 1 1;
}

* {
    margin: 0;
    padding: 0;
}


body {
    font-family: 'Roboto', sans-serif;
}
